.about-section {
    padding: 0;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.about-section h1 {
    font-size: 3.5rem;
    margin-bottom: 1rem;
    text-transform: uppercase;
}

.about-section p {
    font-size: 1.2rem;
    max-width: 800px;
    line-height: 1.8;
    margin-top: 0.5rem;
}
